.correcting-homework-content .paper-right-menu {
  background: #fff;
  border-radius: 20px;
  border: 4px solid #ffffff;
  padding: 15px;
}
.correcting-homework-content .paper-right-menu {
  background: #fff;
  border-radius: 20px;
  border: 4px solid #ffffff;
  padding: 15px;
  margin: 0 auto;
}
.correcting-homework-content .detail-head-row {
  color: #2F3031;
  padding: 14px;
}
.correcting-homework-content .detail-head-row .detail-history {
  color: #646F73;
}
.correcting-homework-content .detail-head-row .detail-head-edit {
  text-align: right;
}
.correcting-homework-content .detail-head-row .detail-head-edit div {
  display: inline-block;
  margin-left: 10px;
  cursor: pointer;
}
.correcting-homework-content .detail-head-row .detail-head-edit div i {
  margin-left: 5px;
}
.correcting-homework-content .preview_footer {
  background: #f1f1f1;
  padding: 0 15px;
  color: #6B706F;
  font-size: 12px;
  height: 36px;
  line-height: 16px;
}
.correcting-homework-content .preview_footer .question-bottom {
  display: flex;
  position: relative;
}
.correcting-homework-content .preview_footer .question-bottom .user-img {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  position: absolute;
  bottom: 5px;
  left: 0;
}
.correcting-homework-content .preview_footer .question-bottom .question-bottom-left {
  margin-left: 52px;
}
.correcting-homework-content .preview_footer .question-bottom .question-bottom-left .user-name {
  color: #6B706F;
  font-size: 12px;
  margin-bottom: 0;
  height: 16px;
  line-height: 16px;
  text-align: left;
}
.correcting-homework-content .preview_footer .question-bottom .question-bottom-left .user-name span {
  font-weight: bold;
  margin-right: 5px;
}
.correcting-homework-content .preview_footer .question-bottom .question-bottom-left .question-bottom-doc span {
  margin-right: 10px;
}
.correcting-homework-content .question-bottom-right {
  color: #DE923F;
  font-size: 12px;
  text-align: right;
  line-height: 36px;
}
.correcting-homework-content .question-bottom-right .ant-btn {
  margin-left: 14px;
  height: 26px;
  padding: 0 10px;
  background-color: #DE923F !important;
}
.correcting-homework-content .preview_border {
  border: 1px solid #dee6ee;
  margin-bottom: 20px;
  border-radius: 10px;
}
.correcting-homework-content .preview_border .preview_top {
  margin: 10px;
  padding: 10px;
}
.correcting-homework-content .preview_img {
  width: 200px;
  box-sizing: border-box;
}
.correcting-homework-content .preview_audio {
  padding: 10px;
}
.correcting-homework-content .errors_span span {
  padding: 0 7px;
  cursor: pointer;
}
